body {
  padding: 20px 80px; }

.App {
  max-width: 860px; }

h2 {
  margin-bottom: 35px; }

.filter-type-toggle {
  padding: 4px;
  border-radius: 4px;
  margin: 0 5px;
  cursor: pointer; }

h3 {
  border-bottom: solid 1px #ddd;
  padding-bottom: 8px;
  margin: 45px 0 0 0; }
  h3 .total-count {
    color: rgba(0, 0, 0, 0.4);
    font-size: 14px;
    margin-left: 5px; }
  h3.excluded-title {
    color: #ef2073; }

.filter-name {
  color: rgba(0, 0, 0, 0.3);
  font-size: 13px;
  font-weight: bold;
  margin-right: 5px;
  display: none; }

.package-item {
  padding: 10px 0;
  border-bottom: solid 1px #DDD;
  position: relative; }
  .package-item:last-child {
    border: none; }
  .package-item .icon {
    position: absolute;
    left: -16px;
    top: 22px;
    font-size: 10px;
    color: rgba(0, 0, 0, 0.2); }
  .package-item .filter-action-string {
    position: absolute;
    width: 200px;
    right: -200px;
    font-size: 15px;
    top: 20px;
    color: #ef2073; }
  .package-item.hidden .filter-action-string {
    color: #4aa908; }
  .package-item:hover button {
    opacity: 1; }
  .package-item .name {
    color: #0161bc;
    min-width: 150px;
    display: inline-block; }
  .package-item .add {
    display: none; }
  .package-item.hidden .add {
    display: block; }
  .package-item.hidden .remove {
    display: none; }

h4 {
  text-transform: capitalize;
  color: rgba(0, 0, 0, 0.4);
  margin: 0 0 6px 0;
  display: none; }

.filter-wrapper {
  border-bottom: solid 1px #ddd; }

.top-filter {
  padding: 8px 15px;
  cursor: pointer;
  color: #0161bc;
  border-right: none;
  margin: 0 15px 0 0;
  display: inline-block;
  position: relative;
  top: 2px;
  color: rgba(0, 0, 0, 0.5); }
  .top-filter:hover {
    color: black; }
  .top-filter.enabled {
    color: black;
    border-bottom: solid 3px black; }

.filter-type {
  color: rgba(0, 0, 0, 0.4);
  margin-right: 0px; }

.filter-tag {
  margin: 0; }
  .filter-tag:after {
    content: ", ";
    text-decoration: none !important; }
  .filter-tag:last-child:after {
    content: ""; }
  .filter-tag.include {
    color: #4aa908; }
  .filter-tag.exclude {
    color: #ef2073; }

.filter-display {
  float: right;
  display: inline-block; }

button {
  padding: 8px 5px;
  min-width: 100px;
  border: none;
  color: white;
  margin: 0 5px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-radius: 3px;
  font-size: 15px;
  outline: none !important;
  cursor: pointer;
  opacity: .3; }
  button:hover {
    outline: none; }
  button.add {
    border: solid 1px #4aa908;
    color: #4aa908; }
    button.add:hover {
      color: white;
      background: #4aa908; }
  button.remove {
    color: #ef2073;
    background: white;
    font-weight: normal;
    border: solid 1px #ef2073; }
    button.remove:hover {
      color: white;
      background: #ef2073; }



